<template>
  <div class="navigation">
    <img src="../../assets/logo.svg"/>
    <!--<ul>
      <li id="menu-btn" v-if="isAuthenticated" @click="menuActive">
          <img src="../../assets/menu.png">
      </li>
    </ul>
    <ul>
      <li v-if="isProfileLoaded">
        <router-link to="/account">{{name}}</router-link>
      </li>
      <li v-if="isAuthenticated" @click="logout">
        <span class="logout">Logout</span>
         </li>
      <li v-if="!isAuthenticated && !authLoading">
        <router-link to="/login">Login</router-link>
      </li>
    </ul> -->
  </div>
</template>

<style lang="scss" scoped>
  a {
    color: white;
    text-decoration: none;
  }
  .navigation {
    display: flex;
    color: white;
    align-items: center;
    background-color: #3367d6;
    height: 5%;
    padding: 5px;
    width: 100%;
    ul{
      display: flex;
      &:first-child{
        flex-grow: 1;
      }
      li {
        padding-right: 1em;
      }
    }
  }
  .brand {
    display: flex;
    align-items: center;

  }
  .logout {
    &:hover {
      cursor: pointer;
    }
  }

  #menu-btn:hover {
    cursor: pointer;
  }

</style>

<script>
  import { mapGetters, mapState } from 'vuex'
  import { AUTH_LOGOUT } from 'actions/auth'
  import { MENU_CHANGE_STATUS } from 'actions/menu'

  export default {
    name: 'navigation',
    methods: {
      menuActive: function () {
        this.$store.dispatch(MENU_CHANGE_STATUS)
      },
      logout: function () {
        this.$store.dispatch(AUTH_LOGOUT).then(() => this.$router.push('/login'))
      }
    },
    computed: {
      ...mapGetters(['getProfile', 'isAuthenticated', 'isProfileLoaded', 'menuIsActive']),
      ...mapState({
        authLoading: state => state.auth.status === 'loading',
        name: state => `${state.user.username}`,
        menuIsActive: state => `${state.menu.menuIsActive}`,
      })
    },
  }
</script>



