import { USER_REQUEST, USER_ERROR, USER_SUCCESS } from '../actions/user'
import Vue from 'vue'
import { AUTH_LOGOUT } from '../actions/auth'

const state = { status: '', username: localStorage.getItem('username') }

const getters = {
  getProfile: state => state.username,
  isProfileLoaded: state => state.username !== '' && state.username !== null && state.username !== 'undefined',
}

const actions = {
  [USER_REQUEST]: ({commit}) => {
    commit(USER_REQUEST)
    commit(USER_SUCCESS, localStorage.getItem('username'))
  },
}

const mutations = {
  [USER_REQUEST]: (state) => {
    state.status = 'loading'
  },
  [USER_SUCCESS]: (state, resp) => {
    state.status = 'success'
    Vue.set(state, 'username', resp)
  },
  [USER_ERROR]: (state) => {
    state.status = 'error'
  },
  [AUTH_LOGOUT]: (state) => {
    state.username = ''
  }
}

export default {
  state,
  getters,
  actions,
  mutations,
}



