<template>
    <div class="conteiner">
        <div class="form-input">
            <p>Name</p>
            <input type="text" name="name" v-model="performerInfo.performer_l10n.name">
        </div>
        <div class="form-input">
            <p>Email</p>
            <input type="text" name="email" v-model="performerInfo.performer.email">
        </div>
        <div class="form-input">
            <p>phone</p>
            <input type="text" name="phone" v-model="performerInfo.performer.phone">
        </div>
        <div class="form-input">
            <p>type</p>
            <input type="text" name="type" v-model="performerInfo.performer.type">
        </div>
        <div class="form-input">
            <p>url</p>
            <input type="text" name="url" v-model="performerInfo.performer.url">
        </div>
        <div class="form-input">
            <p>address</p>
            <input type="text" name="address" v-model="performerInfo.performer_l10n.address">
        </div>
        <div class="form-input">
            <p>description</p>
            <input type="text" name="description" v-model="performerInfo.performer_l10n.description">
        </div>
        <table>
            <caption>SERVICES</caption>
            <tr>
                <th>Name</th>
                <th>Status</th>
            </tr>
            <tr v-for="serviceItem in performerInfo.services">
                <td><router-link :to='`/service/view/${serviceItem.service_id}`'>{{ serviceItem.name }}</router-link></td>
                <td v-if="serviceItem.status == 'active'">ACTIVE</td>
                <td v-else>DISABLED</td>
            </tr>
        </table>
        <div>
            <button type="button" v-on:click="savePerformer">Save</button>
        </div>
    </div>
</template>

<script>
    import {PERFORMER_VIEW_REQUEST, PERFORMER_SAVE_REQUEST} from '../../store/actions/performer'
    import {AUTH_REQUEST} from 'actions/auth'

    export default {
        name: 'performerView',
        data() {
            return {
                performerInfo: {
                    performer: {
                        email: '',
                        phone: '',
                        type: '',
                        url: '',
                    },
                    performer_l10n: {
                        name: '',
                        address: '',
                        description: '',
                    },
                    services: []
                }
            };
        },
        methods: {
            savePerformer: function removeImage(e) {
                console.log(this.performerInfo);
                this.$store.dispatch(PERFORMER_SAVE_REQUEST, this.performerInfo)
                    .then(response => {

                    })
                    .catch(err => {
                        console.log(err);
                    })
            },
        },
        mounted() {
            this.$store.dispatch(PERFORMER_VIEW_REQUEST, this.$route.params.performer_id)
                .then(response => {
                    console.log(response.data);
                    this.performerInfo = response.data
                }).catch(err => {
                console.log(err)
            })
        }
    }
</script>

<style scoped>
    .conteiner {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
        -webkit-animation: activate .6s;
    }
</style>


