import axios from 'axios'

/* const methods = [
  'POST',
  'GET'
] */

const apiCall = ({url, method, ...args}) => new Promise((resolve, reject) => {
  try {
    let request = (method === 'POST') ? axios.post(url, args.data) : axios.get(url)
    request.then(
      resp => {
        resolve(resp)
      }
    ).catch(e => {
      reject(new Error(e))
    })
  } catch (err) {
    reject(new Error(err))
  }
})

export default apiCall



