<template>
        <div class="vue-menu">
            <div class="performer-link" :id="'performer_' + perfornerItem.performer_id" :ref="'performer_' + perfornerItem.performer_id" :href="perfornerItem.name" v-on:click="collapse(perfornerItem.performer_id)" v-for="perfornerItem in performerItemsView">
                <div :class="aClass" ref="content">
                    <div class="main-image">
                        <img :src="perfornerItem.performer_image">
                    </div>
                    <div class="main-info">
                        <div class="item-info">
                            <h3>{{ perfornerItem.name }}</h3>
                            <div class="img-content">
                                <img src="../../assets/loc_menu.svg"><p>{{ perfornerItem.address }}</p>
                            </div>
                            <div class="img-content">
                                <img src="../../assets/uah.svg"><p>{{ perfornerItem.price }}</p>
                            </div>
                            <p>{{ perfornerItem.description_main }}</p>
                            <p class="blurry-text">{{ perfornerItem.description_blur }}</p>
                        </div>
                        <div :class="itemClass">
                            <div class="img-content">
                                <img src="../../assets/tel.svg">
                                <p>{{ perfornerItem.phone }}</p>
                            </div>
                            <div class="img-content">
                                <a :href="perfornerItem.url" v-on:click.stop="noAction()" target="_blank">
                                    <img src="../../assets/url.svg">
                                    <p>{{ perfornerItem.url }}</p>
                                </a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
</template>

<script>
    import {PERFORMER_LIST_REQUEST} from 'actions/performer'

    export default {
      name: 'vue-menu',
        data () {
            return {
              performerItemsView: [],
              itemClass: "item-full-info",
              aClass: "performer-item",
            }
        },
        created () {
            this.fetchData()
        },
      props: {
        mapPerformer: {
          type: Number
        }
      },
      methods: {
          collapse: function (id) {
            this.checkPerformer(this, id);
        },
        noAction: function () {
        },
          fetchData () {
              this.$store.dispatch(PERFORMER_LIST_REQUEST, 'ua-UA').then((resp) => {
                  if (resp.status === 200) {
                      this.performerItemsView = resp.data;
                  }
              })
          },
        checkPerformer (event, id) {
          if (event.$refs['performer_' + id][0].firstChild.attributes['class'].nodeValue === 'performer-item expanded') {
            event.$emit("currentPerformer", 0);
          } else {
            event.$emit("currentPerformer", id);
          }

          event.$refs.content.forEach((item) => {
            if ('performer_' + id !== item.parentNode.id) {
              item.attributes['class'].nodeValue = 'performer-item';
              item.childNodes[2].lastChild.attributes['class'].nodeValue = 'item-full-info';
              item.childNodes[2].firstChild.lastChild.attributes['class'].nodeValue = 'blurry-text';
            }
          })

          if (event.$refs['performer_' + id][0].firstChild.attributes['class'].nodeValue === 'performer-item') {
            event.$refs['performer_' + id][0].firstChild.attributes['class'].nodeValue = this.aClass + ' expanded'
          } else {
            event.$refs['performer_' + id][0].firstChild.attributes['class'].nodeValue = this.aClass
          }

          let el = event.$refs['performer_' + id][0];
          el.scrollIntoView({ behavior: 'smooth', block: 'center', inline: 'center' })

          if (event.$refs['performer_' + id][0].firstChild.childNodes[2].firstChild.lastChild.attributes['class'].nodeValue === 'blurry-text') {
            event.$refs['performer_' + id][0].firstChild.childNodes[2].firstChild.lastChild.attributes['class'].nodeValue = ''
          } else {
            event.$refs['performer_' + id][0].firstChild.childNodes[2].firstChild.lastChild.attributes['class'].nodeValue = 'blurry-text'
          }

          if (event.$refs['performer_' + id][0].firstChild.childNodes[2].lastChild.attributes['class'].nodeValue === 'item-full-info') {
            event.$refs['performer_' + id][0].firstChild.childNodes[2].lastChild.attributes['class'].nodeValue = 'item-full-info expand'
          } else {
            event.$refs['performer_' + id][0].firstChild.childNodes[2].lastChild.attributes['class'].nodeValue = 'item-full-info'
          }

          /*
          setTimeout(() => {
            let el = event.$refs['performer_' + id][0];
            el.scrollIntoView({ behavior: 'smooth', block: 'center', inline: 'center' })
          }, 250)

          setTimeout(() => {
            if (event.$refs['performer_' + id][0].firstChild.childNodes[2].firstChild.lastChild.attributes['class'].nodeValue === 'blurry-text') {
              event.$refs['performer_' + id][0].firstChild.childNodes[2].firstChild.lastChild.attributes['class'].nodeValue = ''
            } else {
              event.$refs['performer_' + id][0].firstChild.childNodes[2].firstChild.lastChild.attributes['class'].nodeValue = 'blurry-text'
            }
          }, 150)

          setTimeout(() => {
            if (event.$refs['performer_' + id][0].firstChild.childNodes[2].lastChild.attributes['class'].nodeValue === 'item-full-info') {
              event.$refs['performer_' + id][0].firstChild.childNodes[2].lastChild.attributes['class'].nodeValue = 'item-full-info expand'
            } else {
              event.$refs['performer_' + id][0].firstChild.childNodes[2].lastChild.attributes['class'].nodeValue = 'item-full-info'
            }
          }, 150)*/
        }
      },
      watch: {
        mapPerformer: function() {
          this.checkPerformer(this, this.mapPerformer);
        }
      },
    }
</script>

<style scoped>
    .vue-menu {
        display: flex;
        flex-direction: column;

        overflow-y: scroll;
        max-height: 700px;
        border-right: 1px solid rgba(204, 204, 204, 0.5);
        width: 60%;
    }
    .performer-link {

    }

    .performer-item {
        display: flex;
        flex-direction: row;
        margin: 2px;
        transition: max-height 0.5s linear;
        position: relative;
        border: 1px solid rgba(204, 204, 204, 0.5);
        max-height: 300px;
        height: auto;
        animation: heigh-animation-end 0.2s;
        z-index: 1;
    }

    .performer-item.expanded {
        max-height: 600px;
        animation: heigh-animation 0.2s;
    }

    .img-content {
        display: flex;
        align-content: flex-start;
        padding-top: 2%;
        align-items: center;
        justify-content: center;
        font-size: 14px;
        color: rgba(0,0,0,.655);
        text-decoration: none;
        animation: opacity_start 0.6s;
    }

    .img-content p {
        padding-left: 5px;
        padding-top: auto;
        padding-bottom: auto;
        margin-top: auto;
        margin-bottom: auto;
    }

    .img-content a {
        display: flex;
        align-content: flex-start;
        pointer-events: all;
    }

    @keyframes heigh-animation-end {
        0% {
            max-height: 500px;
        }
        16% {
            max-height: 450px;
        }
        32% {
            max-height: 400px;
        }
        48% {
            max-height: 350px;
        }
        64% {
            max-height: 300px;
        }
        80% {
            max-height: 300px;
        }
        100% {
            max-height: 300px;
        }
    }

    @keyframes heigh-animation {
        0% {
            max-height: 300px;
        }
        16% {
            max-height: 300px;
        }
        32% {
            max-height: 300px;
        }
        48% {
            max-height: 350px;
        }
        64% {
            max-height: 400px;
        }
        80% {
            max-height: 450px;
        }
        100% {
            max-height: 500px;
        }
    }

    .performer-item:hover {
        cursor: pointer;
        transition:background-color 0.4s ease-out;
        background-color: rgba(199, 193, 193, 0.2);
    }

    .main-info {
        display: flex;
        flex-direction: column;
        justify-content: left;
        align-items: center;
        overflow: hidden;
        max-height: max-content;
        height: auto;
    }

    .item-full-info {
        width: 100%;
        visibility: hidden;
        overflow:hidden;
        flex:0;
        max-height: max-content;
        height: auto;
        animation: opacity_revert 0.3s;
        display: flex;
        flex-direction: column;
        justify-content: left;
        align-items: start;
        margin-left: 10%;
        padding: 2%;
    }

    @keyframes opacity_start {
        0% {
            opacity: 0;
        }
        100% {
            opacity: 1;
        }
    }

    @keyframes opacity_revert {
        0% {
            opacity: 1;
        }
        100% {
            opacity: 0;
        }
    }

    .expand {
        visibility:visible;
        overflow: visible;
        animation: opacity_start 0.3s;
    }

    .item-info {
        height: 100%;
        margin-left: 5%;
        display: flex;
        flex-direction: column;
        justify-content: left;
        align-items: flex-start;
    }
    .item-info h3 {
        text-align: center;
    }
    .item-info p {
        margin: 0;
        padding-top: 2%;
        padding-right: 5%;
        font-size: 14px;
        color: rgba(0,0,0,.655);
        text-decoration: none;
        animation: opacity_start 0.3s;
    }
    .item-info .blurry-text {
        padding-top: 0;
        padding-right: 5%;
        color: transparent;
        background: -webkit-linear-gradient(rgba(0,0,0,.655), rgba(235, 228, 228, 0.4));
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;

        overflow: hidden;
        position: relative;
        line-height: 1.2em;
        max-height: 3.6em;
        text-align: justify;
    }
</style>



