/* eslint-disable promise/param-names */
import { AUTH_REQUEST, AUTH_ERROR, AUTH_SUCCESS, AUTH_LOGOUT } from '../actions/auth'
import { USER_REQUEST } from '../actions/user'
import apiCall from 'utils/api'
import urls from 'utils/urls'

const state = { token: localStorage.getItem('token') || '', status: '', hasLoadedOnce: false, errors: [] }

const getters = {
  isAuthenticated: state => !!state.token,
  authStatus: state => state.status,
  authErrors: state => state.errors
}

const actions = {
  [AUTH_REQUEST]: ({commit, dispatch}, user) => {
    return new Promise((resolve, reject) => {
      commit(AUTH_REQUEST)
      apiCall({url: urls.login, method: 'POST', data: user})
        .then(resp => {
          if (resp.status === 200) {
            localStorage.setItem('token', resp.data.token)
            localStorage.setItem('username', resp.data.name)
            commit(AUTH_SUCCESS, resp)
            dispatch(USER_REQUEST)
            resolve(resp)
          } else {
            commit(AUTH_ERROR, resp.data.errors)
            resolve(resp)
          }
        })
        .catch(err => {
          commit(AUTH_ERROR, err)
          reject(err)
        })
    })
  },
  [AUTH_LOGOUT]: ({commit, dispatch}) => {
    return new Promise((resolve, reject) => {
      apiCall({url: urls.logout, method: 'POST'})
        .then(resp => {
          commit(AUTH_LOGOUT)
          localStorage.removeItem('token')
          localStorage.removeItem('username')
          if (resp.status === 200) {
            commit(AUTH_LOGOUT)
            localStorage.removeItem('token')
            localStorage.removeItem('username')
            resolve()
          } else {
            commit(AUTH_ERROR, resp.data.errors)
            resolve()
          }
        })
        .catch(err => {
          commit(AUTH_ERROR, err)
          localStorage.removeItem('token')
          localStorage.removeItem('username')
          reject(err)
        })
    })
  }
}

const mutations = {
  [AUTH_REQUEST]: (state) => {
    state.status = 'loading'
  },
  [AUTH_SUCCESS]: (state, resp) => {
    state.status = 'success'
    state.token = resp.data.token
    state.username = resp.data.name
    state.hasLoadedOnce = true
  },
  [AUTH_ERROR]: (state) => {
    state.status = 'error'
    state.hasLoadedOnce = true
  },
  [AUTH_LOGOUT]: (state) => {
    state.token = ''
  }
}

export default {
  state,
  getters,
  actions,
  mutations,
}



