const serverName = 'http://api.pettypet.com.ua'
const lang = 'ua-UA'
const urls = {
  login: serverName + '/user/login',
  logout: serverName + '/user/logout',
  getMenu: serverName + '/performer/list',
  serviceList: serverName + '/service/list?lang=' + lang,
  performerList:() => {return `${serverName}/performer/list?lang=${lang}`},
  performerMap: serverName + '/performer/map?lang=' + lang,
  serviceView: (serviceId) => {return serverName + `/service/view/${serviceId}?lang=${lang}`} ,
  performerView: (performerId, lang) => {return serverName + `/performer/view/${performerId}?lang=${lang}`} ,
  performerSave: serverName + `/performer/save?lang=${lang}`,
}

export default urls



