import Vue from 'vue'
import Router from 'vue-router'
import Performer from 'components/performer'
import PerformerView from 'components/performerView'

Vue.use(Router)

const router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/performers',
      name: 'Performers',
      component: Performer,
      meta: {title: 'Performers'},
    },
      {
          path: '/performer/view/:performer_id',
          name: 'performerView',
          component: PerformerView,
          meta: {title: 'PerformerView'},
          props: true
      },
  ],
})

/*router.afterEach((toRoute) => {
  window.document.title = toRoute.meta.title
})*/

export default router



