/* eslint-disable promise/param-names */
import { PERFORMER_LIST_REQUEST, PERFORMER_MAP_REQUEST, PERFORMER_LIST_ERROR, PERFORMER_VIEW_REQUEST, PERFORMER_VIEW_ERROR, PERFORMER_SAVE_REQUEST, PERFORMER_SAVE_ERROR } from '../actions/performer'
import apiCall from '../../utils/api'
import urls from '../../utils/urls'
import {SERVICE_VIEW_ERROR, SERVICE_VIEW_REQUEST} from "../actions/service";

//const state = { token: localStorage.getItem('token') || '', status: '', hasLoadedOnce: false, errors: [] }

/*const getters = {
    isAuthenticated: state => !!state.token,
    authStatus: state => state.status,
    authErrors: state => state.errors
}*/

const actions = {
    [PERFORMER_LIST_REQUEST]: ({commit, dispatch}, lang) => {
        return new Promise((resolve, reject) => {
            commit(PERFORMER_LIST_REQUEST)
            apiCall({url: urls.performerList(lang), method: 'GET'})
                .then(resp => {
                    if (resp.status === 200) {
                        commit(PERFORMER_LIST_REQUEST, resp)
                        resolve(resp)
                    } else {
                        commit(PERFORMER_LIST_ERROR)
                        resolve(resp)
                    }
                })
                .catch(err => {
                    commit(PERFORMER_LIST_ERROR, err)
                    reject(err)
                })
        })
    },
  [PERFORMER_MAP_REQUEST]: ({commit, dispatch}) => {
    return new Promise((resolve, reject) => {
      commit(PERFORMER_MAP_REQUEST)
      apiCall({url: urls.performerMap, method: 'GET'})
        .then(resp => {
          if (resp.status === 200) {
            commit(PERFORMER_MAP_REQUEST, resp)
            resolve(resp)
          } else {
            commit(PERFORMER_LIST_ERROR)
            resolve(resp)
          }
        })
        .catch(err => {
          commit(PERFORMER_LIST_ERROR, err)
          reject(err)
        })
    })
  },
    [PERFORMER_VIEW_REQUEST]: ({commit, dispatch}, performerId, lang) => {
        return new Promise((resolve, reject) => {
            commit(PERFORMER_VIEW_REQUEST)
            apiCall({url: urls.performerView(performerId, lang), method: 'GET'})
                .then(resp => {
                    if (resp.status === 200) {
                        commit(PERFORMER_VIEW_REQUEST, resp)
                        resolve(resp)
                    } else {
                        commit(PERFORMER_VIEW_ERROR)
                        resolve(resp)
                    }
                })
                .catch(err => {
                    commit(PERFORMER_VIEW_ERROR, err)
                    reject(err)
                })
        })
    },
    [PERFORMER_SAVE_REQUEST]: ({commit, dispatch}, performerObject) => {
        return new Promise((resolve, reject) => {
            apiCall({url: urls.performerSave, method: 'POST', data: performerObject})
                .then(resp => {
                    if (resp.status === 200) {
                        commit(PERFORMER_SAVE_REQUEST, resp)
                        resolve(resp)
                    } else {
                        commit(PERFORMER_SAVE_ERROR)
                        resolve(resp)
                    }
                })
                .catch(err => {
                    commit(PERFORMER_SAVE_ERROR, err)
                    reject(err)
                })
        })
    }
}

const mutations = {
    [PERFORMER_LIST_REQUEST]: (state) => {
        state.status = 'loading'
    },
  [PERFORMER_MAP_REQUEST]: (state) => {
    state.status = 'loading'
  },
    [PERFORMER_LIST_ERROR]: (state) => {
        state.status = 'error'
        state.hasLoadedOnce = true
    },
    [PERFORMER_VIEW_REQUEST]: (state) => {
        state.status = 'loading'
    },
    [PERFORMER_VIEW_ERROR]: (state) => {
        state.status = 'error'
        state.hasLoadedOnce = true
    },
    [PERFORMER_SAVE_ERROR]: (state) => {
        state.status = 'error'
        state.hasLoadedOnce = true
    },
}

export default {
    actions,
    mutations,
}



