<template>
  <div class="center-container">
    <gmap-map
            ref="mapRef"
            :center="center"
            :zoom="11"
            style="width:100%;  height: 100%;"
    >
      <gmap-marker
              :key="index"
              v-for="(m, index) in markers"
              :position="m.position"
              :icon="m.icon"
              :animation="m.animation"
              :zIndex="m.zIndex"
              @click="check(m)"
      ></gmap-marker>
    </gmap-map>
  </div>
</template>

<script>
  import {PERFORMER_MAP_REQUEST} from 'actions/performer'
  import {gmapApi} from 'vue2-google-maps'

    export default {
        name: 'center-container',
        data() {
            return {
                center: { lat: 50.44114125, lng: 30.52047014 },
                markers: [],
                places: [],
                //currentPlace: null
            };
        },
      props: {
        currentItem: {
          type: Number
        }
      },
      created () {
        this.getCoordinates()
      },
        methods: {
            getCoordinates() {
              setTimeout(() => {
                getData(this.$store)
                  .then((result) => {
                    for (let i = 0; i < result.length; i++) {
                      this.markers.push(result[i]);
                    }
                  })
              }, 1000)
            },
            check(item) {
              let googleMap = gmapApi();
              if (item.icon === "src/assets/loc.svg") {
                this.$refs.mapRef.$mapPromise
                  .then((map) => {
                    map.panTo(item.position)
                    item.icon = "src/assets/icon_loc_map.svg";
                    item.animation = googleMap.maps.Animation.BOUNCE
                    setTimeout(() => {
                      item.animation = null;
                    }, 2000)
                  })
                this.$emit("mapPerformer", item.performer_id);
              }
            },
        },
      watch: {
        currentItem: function() {
          let googleMap = gmapApi();
          if (this.currentItem === 0) {
            this.$refs.mapRef.$mapPromise
              .then((map) => {
                map.panTo({ lat: 50.44114125, lng: 30.52047014 })
              })
          }

          this.markers.forEach((marker) => {
            if (marker.performer_id === this.currentItem) {
              marker.icon = "src/assets/icon_loc_map.svg";
              this.$refs.mapRef.$mapPromise
                .then((map) => {
                  map.panTo(marker.position);
                  marker.animation = googleMap.maps.Animation.BOUNCE
                  marker.zIndex= 200;

                  setTimeout(() => {
                    marker.animation = null;
                  }, 2000)
              })
            } else {
              marker.zIndex= 100;
              marker.icon = "src/assets/loc.svg";
            }
          })
        }
      },
    };

  function getData(store) {
    let result = [];
    return new Promise((resolve, reject) => {
      store.dispatch(PERFORMER_MAP_REQUEST, {})
        .then((resp) => {
          if (resp.status === 200) {
            resp.data.forEach((item) => {
              result.push(
                {
                performer_id: item.performer_id,
                name: item.name,
                position: {
                  lat: item.lat,
                  lng: item.lng,
                },
                animation: 2,
                zIndex: 100,
                icon: "src/assets/loc.svg",
              })
            })
            resolve(result);
          } else {
            resolve([]);
          }
        })
    })
  }
</script>

<style scoped>
    .center-container {
        display: flex;
        flex-direction: column;

        border-right: 1px solid rgba(204, 204, 204, 0.5);
        width: 40%;
    }
</style>



