/* eslint-disable promise/param-names */
import { SERVICE_LIST_REQUEST, SERVICE_LIST_ERROR, SERVICE_VIEW_REQUEST, SERVICE_VIEW_ERROR } from '../actions/service'
import apiCall from '../../utils/api'
import urls from '../../utils/urls'

//const state = { token: localStorage.getItem('token') || '', status: '', hasLoadedOnce: false, errors: [] }

/*const getters = {
    isAuthenticated: state => !!state.token,
    authStatus: state => state.status,
    authErrors: state => state.errors
}*/

const actions = {
    [SERVICE_LIST_REQUEST]: ({commit, dispatch}) => {
        return new Promise((resolve, reject) => {
            commit(SERVICE_LIST_REQUEST)
            apiCall({url: urls.serviceList, method: 'GET'})
                .then(resp => {
                    if (resp.status === 200) {
                        commit(SERVICE_LIST_REQUEST, resp)
                        resolve(resp)
                    } else {
                        commit(SERVICE_LIST_ERROR)
                        resolve(resp)
                    }
                })
                .catch(err => {
                    commit(SERVICE_VIEW_ERROR, err)
                    reject(err)
                })
        })
    },
    [SERVICE_VIEW_REQUEST]: ({commit, dispatch}, serviceId) => {
        return new Promise((resolve, reject) => {
            commit(SERVICE_VIEW_REQUEST)
            apiCall({url: urls.serviceView(serviceId), method: 'GET'})
                .then(resp => {
                    if (resp.status === 200) {
                        commit(SERVICE_VIEW_REQUEST, resp)
                        resolve(resp)
                    } else {
                        commit(SERVICE_VIEW_ERROR)
                        resolve(resp)
                    }
                })
                .catch(err => {
                    commit(SERVICE_VIEW_ERROR, err)
                    reject(err)
                })
        })
    }
}

const mutations = {
    [SERVICE_LIST_REQUEST]: (state) => {
        state.status = 'loading'
    },
    [SERVICE_LIST_ERROR]: (state) => {
        state.status = 'error'
        state.hasLoadedOnce = true
    },
    [SERVICE_VIEW_REQUEST]: (state) => {
        state.status = 'loading'
    },
    [SERVICE_VIEW_ERROR]: (state) => {
        state.status = 'error'
        state.hasLoadedOnce = true
    },
}

export default {
    actions,
    mutations,
}



