<template>
    <div class="conteiner">
        <table>
            <caption>PERFORMERS</caption>
            <tr>
                <th>Name</th>
                <th>Status</th>
            </tr>
            <tr v-for="performer in performerItemsView">
                <td><router-link :to="'/performer/view/' + performer.performer_id">{{ performer.name }}</router-link></td>
                <td v-if="performer.status == 'active'">ACTIVE</td>
                <td v-else>DISABLED</td>
            </tr>
        </table>
    </div>
</template>

<script>
    import {PERFORMER_LIST_REQUEST} from '../../store/actions/performer'

    export default {
        name: 'performer',
        data() {
            return {
                performerItemsView: null
            };
        },
        mounted() {
            this.$store.dispatch(PERFORMER_LIST_REQUEST)
                .then(response => {
                    this.performerItemsView = response.data
                }).catch(err => {
                console.log(err);
                this.performerItemsView = err
            })
        }
    }
</script>

<style scoped>
    .conteiner {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
        -webkit-animation: activate .6s;
    }

    .service-item {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 10%;
    }
</style>


