import { MENU_REQUEST, MENU_ERROR, MENU_SUCCESS, MENU_CHANGE_STATUS } from '../actions/menu'
import apiCall from '../../utils/api'
import urls from '../../utils/urls'

const state = { menuItems: [], status: '', errors: [], menuLoaded: false, isActive: true }

const getters = {
  menuIsActive: state => state.isActive,
  menuLoaded: state => state.menuLoaded,
  menuItems: state => state.menuItems
}

const actions = {
  [MENU_REQUEST]: ({commit}) => {
    return new Promise((resolve, reject) => {
       apiCall({url: urls.getMenu, method: 'GET'})
        .then(resp => {
          if (resp.data.status === 200) {
            commit(MENU_SUCCESS, resp.data.data)
            resolve(resp)
          } else {
            commit(MENU_ERROR, resp.data.errors)
            resolve(resp)
          }
        })
        .catch(err => {
          commit(MENU_ERROR, err)
          reject(err)
        })
    })
  },
  [MENU_CHANGE_STATUS]: ({commit}) => {
    return new Promise(() => {
      commit(MENU_CHANGE_STATUS)
    })
  }
}

const mutations = {
  [MENU_REQUEST]: (state) => {
    state.status = 'loading'
  },
  [MENU_SUCCESS]: (state, resp) => {
    state.menuItems = resp
    state.status = 'success'
    state.menuLoaded = true
  },
  [MENU_ERROR]: (state, resp) => {
    state.status = 'error'
    state.errors = resp.data.errors
  },
  [MENU_CHANGE_STATUS]: (state) => {
    state.isActive = !state.isActive
  },
}

export default {
  state,
  actions,
  mutations,
  getters
}



