<template>
  <div id="app">
    <navigation/>
    <div class="main-container">
        <vue-menu @currentPerformer="updatePerformer" :mapPerformer="mapCheck"></vue-menu>
      <center-container :currentItem="childData" @mapPerformer="updateMapPerformer" />
    </div>
    <sqreen-footer/>
  </div>
</template>

<script>
import Navigation from 'components/navigation'
import { USER_REQUEST } from 'actions/user'
import SqreenFooter from './components/footer/index.vue'
import VueMenu from 'components/vue-menu'
import Map from 'components/lib/center-container'
import {mapGetters} from 'vuex'

export default {
  components: {
    Map,
    SqreenFooter,
    Navigation,
    VueMenu,
  },
  name: 'app',
  data: function() {
    return {
      childData: 0,
      mapCheck: 0
    };
  },
  computed: {
    ...mapGetters(['menuIsActive', 'isAuthenticated']),
    menuActive: function () {
      return this.menuIsActive
    }
  },
  created: function () {
    if (this.$store.getters.isAuthenticated) {
      this.$store.dispatch(USER_REQUEST)
    }
  },
  methods: {
    updatePerformer (variable) {
      this.childData = variable;
    },
    updateMapPerformer (variable) {
      this.mapCheck = variable;
    }
  },
}
</script>

<style>
  #app {
    animation: opacity_start 1.5s;
  }

  @keyframes opacity_start {
    0% {
      opacity: 0;
    }
    25% {
      opacity: 0;
    }
    50% {
      opacity: 0;
    }
    75% {
      opacity: 0.4;
    }
    85% {
      opacity: 0.7;
    }
    100% {
      opacity: 1;
    }
  }

  body {
    margin: 0;
    font-family: 'Roboto', sans-serif;
    color: #2e426b;
    height: 100%;
  }
  ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
  }
  html { height: 100%; }
  .main-container {
    display: flex;
    flex-direction: row;
    max-width: max-content;
  }
  #app {height: 100%; }
</style>

<style scoped>
  .main-container {
    min-height: 100%;
  }
</style>



